"use client"

import { useState } from "react"
import Link from "next/link"
import { Menu, X, Phone } from "lucide-react"

export function Navbar() {
  const [isOpen, setIsOpen] = useState(false)

  const navItems = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
    { href: "/services", label: "Services" },
    { href: "/contact", label: "Contact" },
  ]

  const handleEnquiry = () => {
    window.location.href = "/contact"
  }

  return (
    <>
      {/* Desktop Navigation */}
      <nav
        className="fixed top-0 left-0 right-0 z-50 hidden md:block backdrop-blur-md border-b"
        style={{
          background: "rgba(255, 255, 255, 0.7)",
          borderColor: "rgba(220, 157, 20, 0.1)",
          boxShadow: "0 4px 30px rgba(0, 0, 0, 0.05)",
        }}
      >
        <div className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
          {/* Logo */}
          <Link href="/" className="text-2xl font-bold" style={{ color: "#DC9D14" }}>
            DELTA
          </Link>

          {/* Nav Links */}
          <div className="flex gap-8 items-center">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className="text-sm font-medium transition-all duration-300 hover:scale-105"
                style={{ color: "#6F6F71" }}
              >
                {item.label}
              </Link>
            ))}
          </div>

          {/* Call & Enquiry Buttons */}
          <div className="flex gap-3 items-center">
            <a
              href="tel:+1234567890"
              className="p-2 rounded-full transition-all duration-300 hover:scale-110"
              style={{
                backgroundColor: "rgba(220, 157, 20, 0.1)",
                color: "#DC9D14",
              }}
              title="Call us"
            >
              <Phone size={20} />
            </a>

            <button
              onClick={handleEnquiry}
              className="px-6 py-2 rounded-lg text-white font-medium transition-all duration-300 hover:shadow-lg hover:scale-105"
              style={{ backgroundColor: "#DC9D14" }}
            >
              Enquiry
            </button>
          </div>
        </div>
      </nav>

      {/* Mobile Navigation */}
      <nav
        className="fixed top-0 left-0 right-0 z-50 md:hidden backdrop-blur-md border-b"
        style={{
          background: "rgba(255, 255, 255, 0.7)",
          borderColor: "rgba(220, 157, 20, 0.1)",
          boxShadow: "0 4px 30px rgba(0, 0, 0, 0.05)",
        }}
      >
        <div className="px-6 py-4 flex items-center justify-between">
          <Link href="/" className="text-xl font-bold" style={{ color: "#DC9D14" }}>
            DELTA
          </Link>
          <button onClick={() => setIsOpen(!isOpen)} className="p-2" style={{ color: "#6F6F71" }}>
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <div
            className="fixed inset-0 top-16 z-40 animate-in fade-in slide-in-from-top-4"
            style={{
              background: "rgba(255, 255, 255, 0.95)",
              backdropFilter: "blur(10px)",
            }}
          >
            <div className="flex flex-col gap-4 p-6">
              {navItems.map((item) => (
                <Link
                  key={item.href}
                  href={item.href}
                  className="text-lg font-medium py-2 transition-colors"
                  style={{ color: "#6F6F71" }}
                  onClick={() => setIsOpen(false)}
                >
                  {item.label}
                </Link>
              ))}

              <div className="flex gap-3 pt-4 border-t" style={{ borderColor: "rgba(220, 157, 20, 0.2)" }}>
                <a
                  href="tel:+1234567890"
                  className="flex-1 p-3 rounded-lg transition-all duration-300 flex items-center justify-center gap-2"
                  style={{
                    backgroundColor: "rgba(220, 157, 20, 0.1)",
                    color: "#DC9D14",
                  }}
                >
                  <Phone size={20} />
                  <span className="font-medium">Call</span>
                </a>
                <button
                  onClick={() => {
                    handleEnquiry()
                    setIsOpen(false)
                  }}
                  className="flex-1 p-3 rounded-lg text-white font-medium transition-all duration-300"
                  style={{ backgroundColor: "#DC9D14" }}
                >
                  Enquiry
                </button>
              </div>
            </div>
          </div>
        )}
      </nav>
    </>
  )
}
